.nav-logo {
    height: 60px;
}

.tabs-root {
    width: 100%;
}

.nav-logo:hover {
    cursor: pointer;
}

.nav-item>.active {
    border-bottom: 1px solid #333;
}

body {
    font-family: "Century Gothic", 'Lato', sans-serif;
}

a {
    text-decoration: none;
    color: #000;
    padding: 10px;
}

.et-hero-tabs,
.et-slide {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    text-align: center;
    h1 {
        max-width: 90%;
        font-size: 2rem;
        margin: 0;
        letter-spacing: 1rem;
    }
    h3 {
        max-width: 70%;
        font-size: 1rem;
        letter-spacing: 0.3rem;
        opacity: 0.6;
        a {
            color: #66B1F1;
            margin: 0;
            padding: 0;
        }
    }
}

.et-hero-tabs-container {
    display: flex;
    flex-direction: row;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 70px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    background: #fff;
    z-index: 10;
    justify-content: space-evenly;
    align-items: stretch;
    &--top {
        display: flex;
        flex-direction: row;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 70px;
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        background: #fff;
        z-index: 10;
        justify-content: space-evenly;
        align-items: stretch;
        position: fixed;
        top: 0;
    }
}

.et-hero-tab {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    color: #000;
    letter-spacing: 0.1rem;
    transition: all 0.5s ease;
    font-size: 0.8rem;
    &:hover {
        color: white;
        background: rgba(102, 177, 241, 0.8);
        transition: all 0.5s ease;
    }
}

.et-hero-tab-slider {
    position: absolute;
    bottom: 0;
    width: 0;
    height: 6px;
    background: #66B1F1;
    transition: left 0.3s ease;
}

@media (min-width: 800px) {
    .et-hero-tabs,
    .et-slide {
        h1 {
            font-size: 3rem;
        }
        h3 {
            font-size: 1rem;
        }
    }
    .et-hero-tab {
        font-size: 1rem;
    }
}

.bg-image {
    background-image: url("img/headshot.jpg");
    position: absolute;
    z-index: -1;
    /* Add the blur effect */
    filter: blur(8px);
    -webkit-filter: blur(8px);
    /* Full height */
    height: 100%;
    width: 100%;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.white-text {
    color: #fff;
}